<template>
  <div>
      <p></p>
      <p>{{counter}}</p>
      <v-btn @click="increment"></v-btn>
      <v-btn @click="decrement"></v-btn>      
  </div>
</template>

<script>
export default {
    name:'dashboard-counter',
    methods:{
        increment(){
            //  this.$store.commit()  mutations 
            this.$store.commit('increment', 1);
        },
        decrement(){
            this.$store.commit('increment', -1);
        }
    },
    computed:{
        //  
        counter(){
            return this.$store.state.count;
        },
    }

}
</script>

